import React from 'react'
import {graphql, useStaticQuery } from 'gatsby'
import Img from 'gatsby-image'
import { GoogleLogin } from 'react-google-login';

import styles from './index.module.css'

const GoogleLoginButton = ({user, setUser}) => {

  const responseGoogleOnSuccess = (response) => {
    console.log(response)
    if(response.profileObj){
      const {email, familyName, givenName } = response.profileObj

      setUser({
        ...user,
        email,
        first_name: givenName,
        last_name: familyName
      })
    }
  }

  const responseGoogleOnFail = (response) => console.err(response)

  const imageData = useStaticQuery(graphql`
    query {
      image: file(relativePath: { eq: "icon-google.png" }) {
        childImageSharp {
          fixed(
            width: 30,
            height:30
          ) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)
  return (
  <GoogleLogin clientId={process.env.GOOGLE_CLIENT_ID}
    render={renderProps => (
      <button
        className={styles.googleButton}
        onClick={renderProps.onClick}
        disabled={renderProps.disabled}>
        <div className={'g-signin2'} data-onsuccess="onSignIn"></div>
        <Img fixed={imageData.image.childImageSharp.fixed} className={styles.googleIcon}/>
        Google
      </button>
    )}
    buttonText="Login"
    onSuccess={responseGoogleOnSuccess}
    onFailure={responseGoogleOnFail}
    cookiePolicy={'single_host_origin'}
  />
  )
}

export default GoogleLoginButton