import React from 'react'
import styles from './index.module.css'

const InputText = ({name, required=false, dataValidate, type, value, field, handleChange, placeholder }) => (
    <div className={styles.InputTextContainer}>
      <span className={styles.txt1}>
        {name}
      </span>
      <div className={`${styles.wrapInput100} ${styles.validateInput}`} data-validate ={dataValidate}>
        <input
          className={styles.input100}
          type={type}
          name={name}
          value={value}
          placeholder={placeholder}
          onChange={e=> handleChange({field, value: e.target.value})}/>
        <span className={styles.focusInput100}></span>
      </div>
    </div>
  )

export default InputText