import React from 'react'
import {graphql, useStaticQuery } from 'gatsby'
import Img from 'gatsby-image'
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props'

import styles from './index.module.css'

const FacebookLoginButton = ({user, setUser}) => {
  const responseFacebook = (response) => {
    const {email, first_name, last_name } = response
    setUser({
      ...user,
      email,
      first_name,
      last_name
    })
  }

  const imageData = useStaticQuery(graphql`
    query {
      image: file(relativePath: { eq: "icon-fb.png" }) {
        childImageSharp {
          fixed(
            width: 30,
            height:30
          ) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)
  return (
    <FacebookLogin
  appId={process.env.FACEBOOK_APP_ID}
  fields="first_name, last_name, email"
  callback={responseFacebook}
  render={renderProps => (
    <button className={styles.facebookButton} onClick={renderProps.onClick}>
    <Img fixed={imageData.image.childImageSharp.fixed} className={styles.facebookIcon}/>
    Facebook
  </button>  )}
/>)
}

export default FacebookLoginButton