import React, { useState, useEffect } from "react"

import SEO from "../../components/seo"

import styles from './index.module.css'

import Container from '../container'
import GoogleLoginButton from '../google-login-button'
import FacebookLoginButton from '../facebook-login-button'
import InputText from '../input-text'
import GenderSwitch from "../gender-switch"
import InputRadio from "../inputRadio"

import positions from '../../constants/positions'
import catching from '../../constants/catching'
import defense from '../../constants/defense'
import offense from '../../constants/offense'
import speed from '../../constants/speed'
import throwing from '../../constants/throwing'

import RadarSkillsChart from '../skillsRadarChart'

import onRegisterUser from "../../utils/register";
import getTournamentInfo from '../../utils/tournament'

const SignIn = () => {
  const [disabled, setDisabled] = useState(true)
  const [ user, setUser ] = useState({
    first_name: '',
    last_name: '',
    email: '',
    gender: '',
    height: '',
    age: '',
    position: '',
    catching: '',
    defense: '',
    offense: '',
    speed: '',
    throwing: '',
  })

  const handleChange = ({field, value}) => {
    setUser({...user, [field]: value})
  }

  const handleRegister = (e) => {
    e.preventDefault()
    console.log(user, 'this is user')
    onRegisterUser(user)
  }

  const [ tournament, setTournament ] = useState([])

  useEffect(() => {
    const getTourneyInfo = async () => {
      const res = await getTournamentInfo()
      setTournament(res.data.data[0])
    }
  getTourneyInfo()

  }, [])

  useEffect(() => {
    for(let key in user) {
      if (key === 'nick_name' || key === 'note') {
        continue;
      } else if(user[key] === '' || user[key] === false || user.age.length !== 2 || user.height.length !== 3) {
          setDisabled(true)
          return
      }
    }
    setDisabled(false)
  }, [user])

  return (
    <div>
      <SEO title="HKUPA New Year Hat"/>
      <Container title="HKUPA New Year Hat">
        <div style={{fontFamily: 'san-serif', fontSize: '15px'}}>
          <p>
            Welcome to HKUPA's last tournament of 2019! Join us as we play one last fun tournament before the new year!
            <br/><br/>
            Where: <strong>Tai Hang Tung Recreation Playgrounds</strong><br/>
            When: <strong>Saturday December 28. 1:30pm to 6:00pm</strong><br/>
            How much: <strong>$50 HKD</strong><br/>
            Prize: <strong>Cash!</strong><br/>
            Registration Deadline: <strong>December 23</strong><br/>
            Payment Deadline: <strong>December 25 (Christmas!)</strong>
          </p>
          <p>
            It'll be a great day of fun for all. Please fill out ALL fields (except nick name if you don't have one).
          </p>
        </div>
      </Container>

      <Container title="Fill basic info with">
        <div className={styles.signInContainer}>
          <div className={styles.authButtonsContainer}>
            <FacebookLoginButton setUser={setUser} user={user}/>
            <GoogleLoginButton setUser={setUser} user={user}/>
          </div>

          <h3 className={styles.title}>
            OR
          </h3>

          <form className="form--registration">
              <InputText
                name="First Name *"
                dataValidate="First name is required"
                type="text"
                value={user.first_name}
                field="first_name"
                handleChange={handleChange}/>

              <InputText
                name="Last name *"
                dataValidate="Last name is required"
                type="text"
                value={user.last_name}
                field="last_name"
                handleChange={handleChange}/>

              <InputText name="Email *"
                dataValidate="Email is required"
                type="email"
                value={user.email}
                field="email"
                handleChange={handleChange}/>

                <InputText name="Nickname"
                dataValidate="Email is required"
                type="text"
                value={user.nick_name}
                field="nick_name"
                handleChange={handleChange}/>

                <GenderSwitch handleChange={handleChange} field="gender"/>

                <InputText name="Height in cm *"
                dataValidate="Height is required"
                type="number"
                value={user.height}
                field="height"
                placeholder="Round to closest whole number (173.7 -> 174)"
                handleChange={handleChange}/>

                <InputText name="Age *"
                dataValidate="Age is required"
                type="number"
                value={user.age}
                field="age"
                placeholder="Round to closest number"
                handleChange={handleChange}/>

                <InputRadio
                name="Position *"
                field="position"
                value={user.position}
                handleChange={handleChange}
                options={positions}
                />

                <InputRadio
                name="Offense *"
                field="offense"
                value={user.offense}
                handleChange={handleChange}
                options={offense}
                />

                <InputRadio
                name="Defense *"
                field="defense"
                value={user.defense}
                handleChange={handleChange}
                options={defense}
                />

                <InputRadio
                name="Throwing *"
                field="throwing"
                value={user.throwing}
                handleChange={handleChange}
                options={throwing}
                />

                <InputRadio
                name="Catching *"
                field="catching"
                value={user.catching}
                handleChange={handleChange}
                options={catching}
                />

                <InputRadio
                name="Speed *"
                field="speed"
                value={user.speed}
                handleChange={handleChange}
                options={speed}
                />

                <RadarSkillsChart
                  userStats={[user.throwing, user.catching, user.offense, user.defense, user.speed]}
                  tournamentAverageStats={[tournament.avg_throwing, tournament.avg_catching, tournament.avg_offense, tournament.avg_defense,tournament.avg_speed]}
                  />

                <InputText name="Quick Notes / comments"
                dataValidate="Not required"
                type="textarea"
                value={user.note}
                field="note"
                handleChange={handleChange}
                />

              <div className={styles.containerLogin100FormBtn}>
                <button className={`${styles.login100FormBtn1} ${ disabled ? styles.disabledd : styles.login100FormBtn}`} onClick={handleRegister} disabled={disabled}
                >
                  Register
                </button>
              </div>
          </form>
        </div>
        </Container>
    </div>
  )
}

export default SignIn