import React from 'react'

import styles from './index.module.css'

const GenderSwitch = ({field, handleChange}) => (
    <div>
      <div className={styles.txt1}>
        Gender *
      </div>
      <div className={styles.genderSwitchContainer}>

      <input id="toggle-on" className={`${styles.toggle} ${styles.toggleLeft} ${styles.genderBtn}`} name="toggle" value="male" type="radio"
        onChange={e=> handleChange({field, value: 'male'})}
      />
      <label htmlFor="toggle-on" className={`${styles.btn} ${styles.btnLeft}`}>Male</label>

      <input id="toggle-off" className={`${styles.toggle} ${styles.toggleRight} ${styles.genderBtn}`} name="toggle" value="female" type="radio"
      onChange={e=> handleChange({field, value: 'female'})}
      />
      <label htmlFor="toggle-off" className={`${styles.btn} ${styles.btnRight}`}>Female</label>
      </div>
    </div>
  )

export default GenderSwitch