import React from 'react'
// import ReactApexCharts from 'react-apexcharts'
import loadable from '@loadable/component'

import styles from './index.module.css'

const ReactApexCharts = loadable(() => import('../../../node_modules/react-apexcharts/src/react-apexcharts'))


const RadarSkillsChart = ({userStats, tournamentAverageStats}) => {
  const options =  {
    chart: {
      id: "radar",
      toolbar: {
        show: false
      }
    },
    xaxis: {
      categories: ['Throwing', 'Catching', 'Offense', 'Defense', 'Speed']
    },
    yaxis: {
      forceNiceScale: true,
      max: 4,
    },
    stroke: {
      width: 2
    },
    fill: {
      opacity: 0.3
    },
    markers: {
      size: 2
    },
  }

  const series = [
    {
      name: "Tournament Average",
      data: tournamentAverageStats
    },
    {
      name: "You",
      data: userStats
    },
  ]

  return (
    <div className={styles.RadarChartContainer}>
      <ReactApexCharts
        options={options}
        series={series}
        type="radar"
        width="300"
        height="350"
      />
    </div>
  )
}

export default RadarSkillsChart