import React from 'react'

import styles from './index.module.css'

const InputRadio = ({options, field, handleChange, name}) => {
  return (
    <div className={styles.mainInputRadioContainer}>
      <div className={styles.txt1}>
        {name}
      </div>
      <div className={styles.inputRadioer}>
      {
        options.map((option) => (
          <div key={option.id} className={styles.radioContainer}>
          <input
            id={option.id}
            type="radio"
            className={styles.radioInput}
            name={field}
            value={option.value}
            onChange={() => handleChange({field, value: option.value})}
            />
            <label className={styles.radiolabel} htmlFor={option.id}>{option.description}</label>

            <div className={styles.checkmark}></div>
          </div>
        ))
      }
      </div>
    </div>
  )
}

export default InputRadio